.paginationPages {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  align-self: center;
  list-style: none;
  position: fixed;
  width: 100vw;
  text-align: center;
}

.paginationNav {
  display: flex;
  align-items: flex-start;
  list-style: none;

  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-self: center;
  margin: auto;
  text-align: center;
  font-family: "Mona-Sans Regular", sans-serif;
}

.paginationText:hover {
  cursor: pointer;
  font-family: "Mona-Sans Regular", sans-serif;
}

.paginationNum {
  /* border: 1px solid black; */
  /* background-color: #e88873; */
  /* padding: 10px; */
  margin: 10px;
  padding-bottom: 40px;
  font-family: "Mona-Sans Regular", sans-serif;
  list-style: none;

  text-align: center;

  /* border-radius: 30%; */
}

.paginationText {
  text-align: center;
  font-family: "Mona-Sans Regular", sans-serif;
  font-weight: bold;
  list-style: none;
}
.paginationNum :hover {
  cursor: pointer;
}
